$main-color:#767676;


 .breadcrumb-nav{
grid-row:2;
max-width:100%;
font-size:1.5em;
}

.first-section .breadcrumb-nav  ol li{
}

.breadcrumb-nav  ol {
list-style-type:none;
display:flex;
flex-wrap:wrap;
align-items:center;
padding:0;
}

.breadcrumb-nav  a {
text-align:left;
font-size:0.6em;
color: #49ca5e;
text-decoration:underline !important;
}

.breadcrumb-nav  a:hover {
font-size:0.7em;
color:#96e0a2;
}


.breadcrumb-nav  span {
text-align:left;
padding:0.2em 0.5em 0em 0.5em;
font-size:0.4em;
color: #49ca5e;
}


h1 {
font-size: 1.53em;
font-family: Arial, "Open Sans", sans-serif;
font-weight: 600;
color: #FFFFF0;
padding: 2em 0em 0em 0em;
line-height:1.2;
}

@media only screen and (max-width: 500px){
h1 {
padding: 1em 0em 0em 0em;
}
}

ul[class="newsletter-admin-list"]{
padding: 1em 0em 0em 3em;
list-style:disc !important;
}

ul[class="newsletter-admin-list"] > li{
padding: 0.5em 0em;
}


@media only screen and (max-width: 500px){
ul[class="newsletter-admin-list"]{
padding: 1em 0em 0em 2em;
}
}//end 


.newsletter-admin-links{
color:#49ca5e;
}

.newsletter-admin-links.visited{
color:#49ca5e;
}

.newsletter-admin-links:hover{
color:#96e0a2;
font-size:1.2em;
}

.newsletter-admin-links:active{
text-decoration:none;
}

.navigation > .navigation_container >.main-nav >ul > li > .nav-link{
color:#ffffff !important;
}


 main{
padding: 0 2%;
}


@media only screen and (min-width: 361px){
body {
@include text-fluid($font-family-reset, 400, $main-color, 361px, 575px, 14px, 22px);
}
}//end 


@media only screen and (min-width: 575px){
body {
@include text-fluid($font-family-reset, 400, $main-color, 575px, 768px, 22px, 28px);
}
}//end 


@media only screen and (min-width: 768px){
body {
@include text-fluid($font-family-reset, 400, $main-color, 768px, 992px, 16px, 20px);

}
}


@media only screen and (min-width: 992px){
body {
@include text-fluid($font-family-reset, 400, $main-color, 992px, 1200px, 12px, 17px);
}
}


@media only screen and (min-width: 1200px){
body {
@include text-fluid($font-family-reset, 400, $main-color, 1200px, 1920px, 10px, 24px);
}
}


@media only screen and (min-width: 1920px){
body {
@include text-fluid($font-family-reset, 400, $main-color, 1920px, 2250px, 19px, 26px);
}
}

@media only screen and (min-width: 2250px){
body {
@include text-fluid($font-family-reset, 400, $main-color, 2250px, 2880px, 12px, 28px);
}
}//end
